@import "../../node_modules/bourbon/core/bourbon";

@import "https://fonts.googleapis.com/css?family=Cabin:400,500,600,700|Nunito:200,600|Exo+2:500,600,700,800,900";


$time_visibility: 0.5s;

$menu_items_num: 6;
$menu_offset_delay: 0.1s;

$color_background_body: #227093; // The body background color of the page
$color_background_menu_overlay:  rgba(17, 18, 17, 0.60); // The menu overlay background color
$color_text_main: #ffffff; // NOT USED YET

$font_primary: 'Cabin', sans-serif;
$font_header: 'Exo 2', sans-serif;


html{
  font-size: 16px;
}
body{
  font-family: $font_primary;
  font-size: 96%;
  font-weight: 400;
  min-height: 100vh;
  background-color: $color_background_body;
}
*, ::before, ::after {
  box-sizing: border-box;
}
a{
  text-decoration: none;
  &:hover{
    text-decoration: none;
  }
}



/**
  Triggers the menu to be visible on top of the screen
  Adds a class to body (with JS)
 */
.trigger-menu{
  @include position(absolute, 2rem null null 2rem);
  font-size: 2.4rem;
  color: #ffffff;
  cursor: pointer;
  transform: scale(0.92);
  @include transition(transform 0.2s);
  &:hover{
    transform: scale(1);
  }
}


/**
  The main menu div element.
  Contains all the menu items
 */
.menu-wrapper{
  @include position(fixed, 0 0 0 0);
  visibility: hidden;
  .menu-content{
    padding: 3rem;
    position: relative;
    z-index: 999;
    color: rgba(255, 255, 255, 1);
    .menu-header{
      text-align: center;
      position: relative;
      color: #ffffff;
      font-family: $font_header;
      padding-left: 1rem;
      padding-right: 1rem;
      opacity: 0;
      visibility: hidden;
      @include transition(opacity $time_visibility, visibility $time_visibility);
      > .menu-title{
        padding-bottom: 1rem;
        margin-bottom: 2rem;
        border-bottom: 1px solid rgba(255, 255, 255, 0.76);
      }
      > .menu-close{
        @include position(absolute, 0.5rem 1rem null null);
        width: 18px;
        height: auto;
        cursor: pointer;
      }
    }
    > .menu-body{
      perspective: 500px;
      display: flex;
      flex-wrap: wrap;
      .menu-item{
        flex: 0 0 33.33333%;
        max-width: 33.33333%;
        opacity: 0;
        visibility: hidden;
        margin-bottom: 2.5rem;
        padding-left: 1rem;
        padding-right: 1rem;
        transform: translateY(100px) rotateX(-45deg);
        @include transition(opacity $time_visibility, transform $time_visibility, visibility $time_visibility);
        .item-title{
          font-size: 1rem;
          font-weight: 800;
          margin-bottom: 0rem;
          padding-bottom: 0.75rem;
          border-bottom: 1px solid rgba(255,255,255,0.50);
        }
        .item-content{
          > ul{
            margin: 0;
            padding: 0;
            list-style: none;
            > li{
              perspective: 500px;
              > a{
                display: block;
                padding: 0.4rem 0;
                border-bottom: 1px solid rgba(255,255,255,0.15);
                color: #ffffff;
                transform: rotateX(120deg);
                backface-visibility: hidden;
                @include transition(transform 0.25s 0.5s);
                transform-origin: bottom;
                > i{
                  margin-right: 0.75rem;
                  width: 20px;
                  color: rgba(255, 255, 255, 0.7);
                }
                .item-list-title{
                  display: block;
                }
                .item-list-excerpt{
                  display: block;
                  font-size: 0.925em;
                  color: #c6c6c6;
                }
              }
            }
          }
        }
        @for $i from 1 through $menu_items_num {
          &.menu-item-#{$i} {
            transition-delay: ($menu_offset_delay * $menu_items_num) - ($i * $menu_offset_delay);
          }
        }
      }
    }
    .menu-footer{
      padding-left: 1rem;
      padding-right: 1rem;
      text-align: center;
      font-size: 0.8em;
      color: rgba(255, 255, 255, 0.8);
      opacity: 0;
      visibility: hidden;
      @include transition(opacity $time_visibility, visibility $time_visibility);
    }
  }
  .menu-overlay{
    @include position(fixed, 0 0 0 0);
    background-color: $color_background_menu_overlay;
    opacity: 0;
    visibility: hidden;
    @include transition(opacity $time_visibility, visibility $time_visibility);
    transition-delay: 0.5s;
  }
}


/**
 Styling while menu is visible
 .menu-extended class is appended to body tag
 */
body.menu-extended{
  overflow: hidden;
  .menu-wrapper{
    overflow-y: auto;
    .menu-content{
      .menu-header{
        visibility: visible;
        opacity: 1;
      }
      .menu-item{
        opacity: 1;
        visibility: visible;
        transform: translateY(0px) rotateX(0deg);
        .item-title{
        }
        .item-content{
          > ul{
            > li{
              > a{
                transform: rotateX(0deg);
                @include transition(transform 0.25s 0.5s);
              }
            }
          }
        }
        @for $i from 1 through $menu_items_num {
          &.menu-item-#{$i} {
            transition-delay: $i * $menu_offset_delay;
            @for $j from 1 through 10 {
              > .item-content > ul > li:nth-child(#{$j}) > a {
                transition-delay: 0.3s + (0.2s * $i) + (0.05s * $j);
              }
            }
          }
        }
      }
      .menu-footer{
        visibility: visible;
        opacity: 1;
      }
    }
    .menu-overlay{
      transition-delay: 0s;
      opacity: 1;
      visibility: visible;
    }
  }
}


